@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css?family=Mr+De+Haviland&display=swap);
body {
  margin: 0px;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  background-image: url(/ellpei.github.io/static/media/bg-min.5f4e3f69.jpg);
  background-size: cover; /* Resize the background image to cover the entire container */
  background-position: center;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page {
  color: black;
  width: 80vw;
  height: 100vh;
  text-align: left;
}

h1 {
  margin-top: 20px;
}
.App {
  text-align: center;
  top: 0;
  width: 100%;
  margin: 0;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  /*background-image: linear-gradient( 219.3deg,  rgba(149,0,199,1) 4.7%, rgba(130,0,226,1) 22.9%, rgba(95,0,199,1) 43.2%, rgba(70,0,199,1) 63.9%, rgba(5,0,199,1) 85.1%, rgba(0,95,199,1) 106.8% );
  background-image: linear-gradient(15deg, #5BB7AC 0%, #13547a 100%);*/
  /*background-color: #dbdee3;*/

  min-height: 100vh;
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.Home {
  height: 85vh;
  width: 80vh;
}
.Home .textbox {
  margin-top: 70%;
  margin-left: 20%;
  color: black;
  display:block;
  -webkit-animation: slidein 1s ease-out forwards;
          animation: slidein 1s ease-out forwards;
  /*background-color: black;*/
  border-left: solid;
  border-width: 8px;
  text-align: left;
  width: 60%;
  height: 70%;
  font-family: 'Mr De Haviland', cursive;
  font-size: 60px;
  line-height: 0.1;
}

@-webkit-keyframes slidein{
  0%{ opacity:0; -webkit-transform:translateY(-25px); transform:translateY(-25px); }
  100%{ opacity:1; -webkit-transform:translateY(0px); transform:translateY(0px); }
}

@keyframes slidein{
  0%{ opacity:0; -webkit-transform:translateY(-25px); transform:translateY(-25px); }
  100%{ opacity:1; -webkit-transform:translateY(0px); transform:translateY(0px); }
}

.Projects {
  color: black;
  height: 100vh;
  width: 80vw;
  -webkit-animation: slidein 1s ease-out forwards;
          animation: slidein 1s ease-out forwards;

}
.Projects h1 {
  text-align: left;

}
.bubble-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}
.bubble {
  width: 200px;
  height: 200px;
  border-style: solid;
  border-color: black;
  color: black;
  background-color: transparent;
  font-size: 24px;
  border-radius: 20px 0px 20px 0px;
  border-width: 3px;
  padding: 20px;
  margin: 10px;

}

.infobox div {
  padding: 5% 10% 0% 10%;
  border-radius: 10px;
  height: 500px;
  -webkit-animation: slidein 1s ease-out forwards;
          animation: slidein 1s ease-out forwards;

}

@-webkit-keyframes slidein{
  0%{ opacity:0; -webkit-transform:translateY(-25px); transform:translateY(-25px); }
  100%{ opacity:1; -webkit-transform:translateY(0px); transform:translateY(0px); }
}

@keyframes slidein{
  0%{ opacity:0; -webkit-transform:translateY(-25px); transform:translateY(-25px); }
  100%{ opacity:1; -webkit-transform:translateY(0px); transform:translateY(0px); }
}

button:hover {
  background-color: black;
  color: white;
  transition: background-color 0.4s linear; 

}

button:active {
  outline: none;
  background-color: black;

}

button:focus {
  outline: none;
  background-color: black;
  color: white;
}

.github-link {
  background-color: transparent;
  border-style: none;
  border-radius:100%;
  color: black;
  font-size: 30px;
}

.Navbar {
  text-align: center;
  width: 100%;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float:left;
}

li a {
  display: block;
  text-align: center;
}

.top-navbar {
	width: 100%;
  position: fixed;
  z-index: 10;
  left: 0px;
  top: 0px;
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 24px;

}
.top-navbar .icon {
	display: none;
	background-color: transparent;
	border-style: none;
	font-size: 40px;
	color: black;
	padding: 0px 25px 0px 10px;
}

a {
  margin-left: 20px;
  margin-right: 20px;
  text-decoration: none;
  color: #2b2b2b;
  font-weight: lighter;
}

a:active {
  color: #E05C5D;
}

a:hover {
	text-decoration: underline;
}

.active {
  color: #E05C5D;
  font-weight: bold;
}

a:focus {
  font-weight: bold;
  outline: none;
}

.go-top-btn {
  float: right;
  position: fixed;
  font-size: 60px;
  right: 30px;
  bottom: 0px;
  color: black;
}

@media screen and (max-width: 600px) {

	#nav-items {
		display: none;
	}

	.top-navbar .icon {
		float: right;
		display: block;
	}
}

@media screen and (max-width: 600px) {
	#nav-items.responsive {
		display: block;
		width: 100%;
		float: right;
	}

	#nav-items.responsive li {
		width: 100%;
		float: right;
		margin-top: 10px;
		margin-bottom: 10px;
	}


}
